@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  margin: 0;
  /* font-family: 'Share Tech Mono', monospace; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(https://github.com/Michael-Veiga/Portfiolio/blob/master/images/crocodile.jpg?raw=true);
  background-size: cover !important;
  background-repeat: no-repeat;
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.g-card {
  margin: 20px;
  transition: border-width 0.6s linear;
  box-shadow: 0 0 15px 1px #65f135;
  background-color: transparent;
}

.g-card-image {
  border-radius: 10px;
  width: 575px;
  height: 325px;
  box-shadow: 0px 0px 3px 1px #ccc;
}

.g-card-info {
  margin-top: 5px;
  min-height: 75px;
}

.g-card-title {
  font-size: 24px;
  margin: 0px;
}

.g-card-sub-title {
  font-size: 16px;
  margin: 0px;
}

.p-pic-image {
  height: 300px;
  width: 300px;
}

.success-msg {
  color: #57a773;
  padding: 10px 15px;
}

.error-msg {
  color: #ee6352;
  padding: 10px 15px;
}

.fab {
  color: white;
}

.far {
  color: white;
}

.growCard {
  transition: all 0.2s ease-in-out;
}

.growCard:hover {
  transition: all 0.2s ease-in-out;
  transform: scale(1.1);
  -webkit-filter: drop-shadow(5px 5px 4px #65f135);
          filter: drop-shadow(5px 5px 4px #65f135);
}

.grow {
  transition: all 0.2s ease-in-out;
}

.grow:hover {
  transition: all 0.2s ease-in-out;
  transform: scale(1.1);
  -webkit-filter: drop-shadow(5px 5px 2px #65f135);
          filter: drop-shadow(5px 5px 2px #65f135);
}

.nav-effect {
  transition: border-width 0.6s linear;
  box-shadow: 0 0 13px 0.25px #65f135;
  width: 100%;
}

.foot-effect {
  transition: border-width 0.6s linear;
  box-shadow: 0 -3px 13px 0.25px #65f135;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 3rem;
  overflow: hidden;
  background: linear-gradient(
    90deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(1, 41, 1, 1) 50%,
    rgba(0, 0, 0, 1) 100%
  );
}

.item-grow {
  transition: all 0.2s ease-in-out;
}

.item-grow:hover {
  transform: scale(1.1);
  text-shadow: 5px 5px 15px #65f135;
}

.bb {
  max-width: 1110px;
  background-color: rgba(3, 85, 14, 0.616) !important;
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
  transition: border-width 0.6s linear;
  box-shadow: 0 0 15px 1px #65f135;
  font-family: 'Open Sans', sans-serif;
}

.bb:hover {
  box-shadow: 0 0 15px 1px white;
  background-color: rgba(98, 0, 255, 0.3) !important;
  -webkit-backdrop-filter: blur(6px);
          backdrop-filter: blur(6px);
}

.contactCont {
  background-color: rgba(3, 85, 14, 0.616) !important;
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
  box-shadow: 0 0 15px 1px #65f135;
  font-family: 'Open Sans', sans-serif;
}

.abtCard {
  font-family: 'Open Sans', sans-serif;
}


